:host {
	display: block;
}

$colors: (
	'brand': (
		'color': #003c71,
		'textColor': #ffffff,
		'colorHover': #062136,
		'colorHoverAlpha': #c8d4df
	),
	'buyer': (
		'color': #009538,
		'textColor': #ffffff,
		'colorHover': #23655e,
		'colorHoverAlpha': #c8e6d3
	),
	'seller': (
		'color': #005fb2,
		'textColor': #ffffff,
		'colorHover': #003360,
		'colorHoverAlpha': #c8dbec
	),
	'payment': (
		'color': #542f7d,
		'textColor': #ffffff,
		'colorHover': #39185d,
		'colorHoverAlpha': #d9d1e1
	),
	'order': (
		'color': #157dac,
		'textColor': #ffffff,
		'colorHover': #1e6c8f,
		'colorHoverAlpha': #cce1ea
	),
	'shipping': (
		'color': #3b8179,
		'textColor': #ffffff,
		'colorHover': #108352,
		'colorHoverAlpha': #d4e2e0
	)
);

@each $name, $color-map in $colors {
	.#{$name} {
		background-color: var(--#{$name}Color, map-get($color-map, 'color'));
		color: var(--#{$name}TextColor, map-get($color-map, 'textColor'));
		&:hover {
			background-color: var(--#{$name}ColorHover, map-get($color-map, 'colorHover'));
			color: var(--#{$name}TextColor, map-get($color-map, 'textColor'));
		}
	}

	.#{$name}-secondary {
		background-color: var(--#{$name}TextColor, map-get($color-map, 'textColor'));
		color: var(--#{$name}Color, map-get($color-map, 'color'));
		border: 1px solid var(--#{$name}Color, map-get($color-map, 'color'));
		&:hover {
			background-color: var(--#{$name}ColorHoverAlpha, map-get($color-map, 'colorHoverAlpha'));
			color: var(--#{$name}Color, map-get($color-map, 'color'));
			border: 1px solid var(--#{$name}ColorHover, map-get($color-map, 'colorHover'));
		}
	}

	.#{$name}-text {
		color: var(--#{$name}Color, map-get($color-map, 'color'));
		background-color: transparent;
		border: none;
		&:hover {
			color: var(--#{$name}ColorHover, map-get($color-map, 'colorHover'));
		}
	}

	.#{$name}-disabled {
		background-color: #f5f5f5;
		color: #999;
		border: 1px solid #ddd;
		cursor: not-allowed;
		&:hover {
			background-color: #f5f5f5;
			color: #999;
			border: 1px solid #ddd;
		}
	}

	.#{$name}-text-disabled {
		color: #999;
		background-color: transparent;
		border: none;
		cursor: not-allowed;
		&:hover {
			color: #999;
		}
	}
}
